*{
    margin: 0;
    padding: 0;
}

article{
    width: 60%;
    margin: auto;
}

.cart_box img{
    width: 50px;
    height: 50px;
}

.cart_img{
    display: flex;
    width: 400px;
}

.cart_img p{
    font-weight: bold;
    margin-left: 10px;
}

.cart_box{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
    border-bottom: 2px solid red;
    padding-bottom: 5px;
}

.cart_box span{
    margin-right: 6px;
}

.total{
    display: flex;
    justify-content: space-between;
}

.total span:nth-child(1){
    color: orange;
    font-weight: bold;
    letter-spacing: 2px;
    font-size: 2rem;
}

.total span:nth-child(2){
    color: red;
    font-weight: bold;
    font-size: 2rem;
}